import React, { useState, useEffect, useCallback } from 'react';
import { Container, Form, Button, Alert, Row, Col } from 'react-bootstrap';
import axios from 'axios';


const findServerPort = async () => {
  for (let port = 5000; port < 5100; port++) {
    try {
      const response = await axios.get(`http://localhost:${port}/api/test`);
      if (response.data.message === 'Server is working') {
        return port;
      }
    } catch (error) {
      // Port is not responding, continue to the next one
    }
  }
  throw new Error('Unable to find server port');
};

const API_URL = await findServerPort().then(port => `http://localhost:${port}`);
console.log('API_URL:', API_URL);

// Rest of your Translator component...

function Translator() {
  const [inputText, setInputText] = useState('');
  const [translatedText, setTranslatedText] = useState('');
  const [inputLanguage, setInputLanguage] = useState('en-US');
  const [targetLanguage, setTargetLanguage] = useState('es');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [isListening, setIsListening] = useState(false);
  const [mediaRecorder, setMediaRecorder] = useState(null);

  const languages = {
    'en-US': 'English',
    'es-ES': 'Spanish',
    'fr-FR': 'French',
    'de-DE': 'German',
  };

  

  const sendAudioToServer = useCallback(async (audioBlob) => {
    console.log('Audio blob size:', audioBlob.size);
    console.log('Audio MIME type:', audioBlob.type);
    const reader = new FileReader();
    reader.readAsDataURL(audioBlob);
    reader.onloadend = async () => {
      const base64Audio = reader.result.split(',')[1];
      try {
        console.log('Sending audio to server...', {
          url: `${API_URL}/api/speech-to-text`,
          audioLength: base64Audio.length,
          inputLanguage
        });
        const response = await axios.post(`${API_URL}/api/speech-to-text`, {
          audio: base64Audio,
          encoding: 'LINEAR16',
          sampleRateHertz: 48000,
          languageCode: inputLanguage
        });
        console.log('Received response:', response.data);
        if (response.data.transcription) {
          console.log('Received transcription:', response.data.transcription);
          setInputText(response.data.transcription);
        } else {
          console.log('No transcription received');
          setError('No speech detected. Please try again.');
        }
      } catch (error) {
        console.error('Speech-to-text error:', error.response ? error.response.data : error.message);
        setError('An error occurred during speech recognition. Please try again.');
      }
    };
  }, [inputLanguage, API_URL]);

  useEffect(() => {
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices.getUserMedia({ audio: true })
        .then(stream => {
          const recorder = new MediaRecorder(stream, { mimeType: 'audio/webm' });
          setMediaRecorder(recorder);
  
          let audioChunks = [];
  
          recorder.ondataavailable = (e) => {
            if (e.data.size > 0) {
              audioChunks.push(e.data);
            }
          };
  
          recorder.onstop = () => {
            console.log('Recording stopped, processing audio...');
            const audioBlob = new Blob(audioChunks, { type: 'audio/webm' });
            console.log('Audio blob size:', audioBlob.size);
            sendAudioToServer(audioBlob);
            audioChunks = [];
          };
        })
        .catch(error => {
          console.error('Error accessing microphone:', error);
          setError('Unable to access microphone. Please check your browser settings.');
        });
    } else {
      setError('Speech recognition is not supported in this browser.');
    }
  }, [sendAudioToServer]);

  const toggleListening = useCallback(() => {
    if (isListening) {
      console.log('Stopping recording...');
      mediaRecorder.stop();
      setIsListening(false);
    } else {
      console.log('Starting recording...');
      mediaRecorder.start(1000); // Record in 1-second chunks
      setIsListening(true);
    }
  }, [isListening, mediaRecorder]);

  const handleInputLanguageChange = (e) => {
    const newLanguage = e.target.value;
    setInputLanguage(newLanguage);
  };

  const handleTranslate = async () => {
    if (inputLanguage.split('-')[0] === targetLanguage) {
      setError('Input and output languages are the same. Please choose different languages.');
      return;
    }

    setIsLoading(true);
    setError('');
    try {
      console.log('Sending translation request to:', `${API_URL}/api/translate`);
      const response = await axios.post(`${API_URL}/api/translate`, {
        text: inputText,
        sourceLanguage: inputLanguage.split('-')[0],
        targetLanguage
      });
      console.log('Received translation:', response.data.translation);
      setTranslatedText(response.data.translation);
    } catch (error) {
      console.error('Translation error:', error.response ? error.response.data : error.message);
      setError('An error occurred during translation');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Container className="mt-5">
      <h1>SpeakEasy</h1>
      <Form>
        <Row className="mb-3">
          <Col>
            <Form.Group>
              <Form.Label>Input Language</Form.Label>
              <Form.Select
                value={inputLanguage}
                onChange={handleInputLanguageChange}
              >
                {Object.entries(languages).map(([code, name]) => (
                  <option key={code} value={code}>{name}</option>
                ))}
              </Form.Select>
            </Form.Group>
          </Col>
          <Col>
            <Form.Group>
              <Form.Label>Output Language</Form.Label>
              <Form.Select
                value={targetLanguage}
                onChange={(e) => setTargetLanguage(e.target.value)}
              >
                {Object.entries(languages).map(([code, name]) => (
                  <option key={code.split('-')[0]} value={code.split('-')[0]}>{name}</option>
                ))}
              </Form.Select>
            </Form.Group>
          </Col>
        </Row>
        <Form.Group className="mb-3">
          <Form.Control
            as="textarea"
            rows={3}
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            placeholder="Speak or type your text here"
          />
        </Form.Group>
        <Button 
          variant={isListening ? "danger" : "secondary"}
          onClick={toggleListening}
          className="me-2 mb-3"
        >
          {isListening ? 'Stop Listening' : 'Start Listening'}
        </Button>
        <Button 
          variant="primary" 
          onClick={handleTranslate}
          disabled={isLoading || !inputText.trim()}
        >
          {isLoading ? 'Translating...' : 'Translate'}
        </Button>
      </Form>
      {error && <Alert variant="danger" className="mt-3">{error}</Alert>}
      {translatedText && (
        <Alert variant="success" className="mt-3">
          {translatedText}
        </Alert>
      )}
    </Container>
  );
}

export default Translator;